import React from 'react';
import { IntlProvider, addLocaleData } from 'react-intl';
import en from 'react-intl/locale-data/en';
import ru from 'react-intl/locale-data/ru';

const enTranslation = {
  //authorisation page
  authorisationStudentTitle: 'BSMU Student Personal Account',
  authorisationButton: 'Login',
  insertLogin: 'Insert login:',
  insertPassword: 'Password:',
  //logout component
  logout: 'Log out',
  //messages page
  journal: 'Gradebook',
  activity: "Events",
  messagesNotFound: 'Message could not be found',
  checkUnreadMessages: 'Unread only',
  loadingMore: 'Loading more',
  //message page
  back: 'Back',
  previousMessage: 'previous',
  nextMessage: 'next',
  messages: 'Messages',
  //schedule page
  schedule: 'Schedule',
  course: 'Course',
};

const ruTranslation = {
  //authorisation page
  authorisationStudentTitle: '   ',
  authorisationButton: '',
  insertLogin: ' :',
  insertPassword: ':',
  //logout component
  logout: '  ',
  //messages page
  journal: ' ',
  activity: '',
  checkUnreadMessages: ' ',
  loadingMore: ' ',
  messagesNotFound: '  ',
  //message page
  back: '',
  previousMessage: '',
  nextMessage: '',
  messages: '',
  //schedule page
  schedule: '',
  course: ''

};

addLocaleData([...en, ...ru]);

const Context = React.createContext();

class IntlProviderWrapper extends React.Component {
  constructor(...args) {
    super(...args);

    this.switchToEnglish = () =>
      this.setState({ locale: 'en', messages: enTranslation });

    this.switchToRussian = () =>
      this.setState({ locale: 'ru', messages: ruTranslation });

    // pass everything in state to avoid creating object inside render method (like explained in the documentation)
    this.state = {
      locale: 'ru',
      messages: ruTranslation,
      switchToEnglish: this.switchToEnglish,
      switchToRussian: this.switchToRussian
    };
  }

  render() {
    const { children } = this.props;
    const { locale, messages } = this.state;
    return (
      <Context.Provider value={this.state}>
        <IntlProvider
          key={locale}
          locale={locale}
          messages={messages}
          defaultLocale="ru"
        >
          {children}
        </IntlProvider>
      </Context.Provider>
    );
  }
}

export { IntlProviderWrapper, Context as IntlContext };
