import React from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom'
import Logout from '../Logout/Logout';
import s from './header.module.scss';

const MaillingHeader = ({ rector, location }) => {
	const active = `${s.link} ${s.selected}`;
	const unActive = `${s.link}`;

	return (
		<div className={s.wrapper}>
			<div className={s.linksBlock}>
				{!rector && <div className={location.pathname === '/mailing' ? active : unActive}>
					<NavLink to="/mailing">
						
					</NavLink>
				</div>}
				{!rector && <div className={location.pathname === '/mail-history' ? active : unActive}>
					<NavLink to="/mail-history">
						 
					</NavLink>
				</div>}
				{!rector && <div className={location.pathname === '/groups' ? active : unActive}>
					<NavLink to="/groups">
						 
					</NavLink>
				</div>}
			</div>
			<Logout />
		</div>
	);
};

export default withRouter (MaillingHeader);
