import React from "react";
import { withRouter } from "react-router";
import Logout from "../Logout/Logout";
import s from "./header.module.scss";
import { NavLink } from "react-router-dom";
import { FormattedMessage } from "react-intl";

const MessagesHeader = ({ location, rector, linkToJournal, headWrapper = s.wrapper }) => {
  const active = `${s.link} ${s.selected}`;
  const unActive = `${s.link}`;

  return (
    <div className={headWrapper}>
      <div className={s.linksBlock}>
        {!rector && (
          <div className={location.pathname === "/messages" ? active : unActive}>
            <NavLink to={"/messages"}>
              <FormattedMessage id="messages" />
            </NavLink>
          </div>
        )}

        <div className={unActive}>
          <a href={linkToJournal}>
            <FormattedMessage id="journal" />
          </a>
        </div>
        <div className={unActive}>
          <a href='https://www.bsmu.by/activity/#current'>
            <FormattedMessage id="activity" />
          </a>
        </div>

        {!rector && (
          <div className={location.pathname === "/schedule" ? active : unActive}>
            <NavLink to={"/schedule"} activeClassName="active">
              <FormattedMessage id="schedule" />
            </NavLink>
          </div>
        )}

        <div className={active}></div>
      </div>
      <Logout />
    </div>
  );
};

export default withRouter(MessagesHeader);
